.nav-buttons{
    text-align:center;
    margin: 0 20px 0 20px;
    font-size: 1.2rem;
    -webkit-transition: 0.5s ease;
}
.nav-buttons:hover{
  cursor: pointer;
}
.underbar{
    width: 0;
    height: 5px;
    background: rgba(100,100,200,0);
    top: 20px;
    position: absolute;
    -webkit-transition: 0.5s ease;
  }

.nav-buttons:hover{
    text-shadow: 0px -2px 0px rgba(0,0,0,1), 0 0 5px rgba(255,255,255,0.8),0 -4px 15px rgba(255,255,255,0.5);
}

.nav-buttons {
    position: relative;
  }
  
  .nav-buttons::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: #ffcc00;
    cursor:pointer;
    visibility: hidden;
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    transition: all 0.3s ease-in-out 0s;
  }

  .nav-buttons:hover::before {
    visibility: visible;
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
  }

  .nav-main-bar{
    width: 100%;
    position: fixed !important;
    background-color: transparent;
    transition: all 0.3s ease-in-out;
  }
  .nav-main-bar .nav-buttons{
    color:#FFF !important;
  }
  .nav-main-bar .nav-buttons a{
    color:#FFF !important;
  }
  .nav-main-bar.active{
    transition: all 0.3s ease-in-out;
    background-color: #FFF !important;
    box-shadow: 0px 5px 10px -2px rgba(0, 0, 0, 0.164);
  }
  
  .nav-main-bar.active .nav-buttons{
    color:rgb(37, 37, 37) !important;
  }
  .nav-main-bar.active .nav-buttons a{
    color:rgb(37, 37, 37) !important;
  }
  


  
